/* apply a natural box layout model to all elements, but allowing components to change */
html {
  box-sizing: border-box;
}
*, *:before, *:after {
  box-sizing: inherit;
}

body {
  margin: 0;
}

h1,
h2,
h3,
h4,
p,
blockquote,
figure,
ol,
ul {
    margin: 0;
    padding: 0;
}

main,
li {
    display: block;
}

h1,
h2,
h3,
h4 {
    font-size: inherit;
}

a,
button {
    color: inherit; /* doesnt inherit by default */
    transition: .3s;
}
a {
    text-decoration: none;
}

/*
One of the fundamentals of responsive web development, this makes sure images respect the bounds of a fluid container. The image never exceeds the width of it's container. The image's aspect ratio is preserved even if it has an inline height set.
border: 0 fixes images within links gaining a border in IE 8, 9 and 10.
*/

img {
    max-width: 100%;
    height: auto;
    border: 0;
}

body {
  background-color: rgb(255, 253, 249);
  color: #242331;
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}
.App {
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  min-height: 100vh;
}

.App > footer {
  margin-top: 1rem;
}

.app-header {
  border-top: 5px solid #242331;
  background-color: rgba(255, 253, 249, 0.7);
  position: fixed;
  top: 0;
  width: 100%;
  display: flex;
  flex-wrap: wrap;
  justify-content: space-evenly;
  align-content: space-evenly;
}
.app-header > aside { 
  /* border: 1px solid red; */
  flex-grow: 3;
  flex-basis: 20em;
}
.app-header > section {
  /* border: 1px solid green; */
  flex-grow: 1;
  flex-basis: 20em;
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  max-width: inherit;
}

.result-stats {
  font-size: 1.1rem;
  color: rgb(142, 141, 141);
  margin: 4px 0;
}

.search-box {
  display: flex;
  align-items: baseline;
}

.search-box input {
  width: 100%;
  font-size: 2.1rem;
  outline: none;
  border: none;
  background-color: transparent;
}

main {
  margin-top: 0.4rem;
}

.emoji-details {
  
  display: flex;
  flex-direction: column;
}

.emoji-details p {
  color: gray;
  overflow: hidden;
  white-space: nowrap;
  text-overflow: ellipsis;
  display: none;
}

.emoji-details h2 {
  font-size: 1.4rem;
  margin: 0.1rem 0;
}
.emoji-details span {
  display: none;
  font-size: 1.5rem;
  line-height: 1;
}
.emoji-details h4 {
  font-size: 1.1rem;
  margin-top: auto;
  color: rgb(85, 85, 85);
}


.search-results {
  display: flex;
  flex-wrap: wrap;
  line-height: 1;
  justify-content: space-evenly;
}

.search-results::after {
  content: "";
  flex: auto;
}

.emoji-item {
  cursor: pointer;
  vertical-align: text-top;
  color: #ccc;
  min-width: 3.7rem;
  height: 3.7rem;
  display: flex;
  justify-content: center;
  align-items: center;
  font-size: 2.9rem;
  transition: all 0.2s ease-out;
  font-family: "Noto Color Emoji", "Apple Color Emoji", "Segoe UI Emoji",
  Times, Symbola, Aegyptus, Code2000, Code2001, Code2002, Musica, serif;
}

.emoji-item:hover {
  text-shadow: 0 0 27px #FF3D00;
}

.content {
  padding: 0.5rem;
}

.snackbar-close-btn {
  cursor: pointer;
}

.hidden {
  visibility: hidden;
}

@media screen and (min-width: 30em) {
  .search-box input {
    font-size: 2.8rem;
    margin-bottom: 2.2rem;
  }
  .emoji-details {
    text-align: right;
  }
  .emoji-details p {
    display: block;
  }
  .emoji-details h2 {
    font-size: 1.8rem;
  }
  .emoji-details h4 {
    font-size: 1.4rem;
  }  
  .emoji-details span {
    display: block;
  }  
}


